import React, { useMemo, useState } from 'react';
import {
  Typography,
  AppBar,
  Toolbar,
  IconButton,
  Button,
  //   Link,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Menu as MenuIcon } from '@material-ui/icons';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import { useMediaQuery } from '@material-ui/core';
import theme from '../../theme';

const useStyles = makeStyles((theme) => ({
  logo: { display: 'flex', flexGrow: 1 },
  navigationItems: { display: 'flex', flexGrow: 9, flexDirection: 'row' },
  publicItems: { display: 'flex' },
  accountItems: { display: 'flex', marginLeft: 'auto' },
  offset: theme.mixins.toolbar,
}));

const NavBar = withRouter(({ history }, props) => {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const classes = useStyles();
  const [menuIsHide, setMenuIsHide] = useState(true);

  const isActive = useMemo(
    () => (history, path) => {
      if (history.location.pathname === path)
        return {
          color: prefersDarkMode
            ? theme.palette.secondary.dark
            : theme.palette.secondary.light,
        };
      else return { color: '#ffffff' };
    },
    [prefersDarkMode]
  );

  return (
    <div>
      <AppBar position="fixed">
        <Toolbar variant="dense">
          <Button
            component={RouterLink}
            to="/"
            style={isActive(history, '/')}
            className={classes.logo}
            color="inherit"
          >
            <Typography variant="h6">Initcard</Typography>
            {menuIsHide ? null : (
              <IconButton edge aria-label="menu" color="inherit">
                <MenuIcon />
              </IconButton>
            )}
          </Button>
          <div className={classes.navigationItems}>
            <div className={classes.publicItems}>
              <Button
                component={RouterLink}
                to="/latest"
                color="inherit"
                style={isActive(history, '/latest')}
              >
                <Typography>Latest</Typography>
              </Button>
              <Button
                component={RouterLink}
                to="/features"
                color="inherit"
                style={isActive(history, '/features')}
              >
                <Typography>Features</Typography>
              </Button>
              <Button
                component={RouterLink}
                to="/regions"
                color="inherit"
                style={isActive(history, '/regions')}
              >
                <Typography>Regions</Typography>
              </Button>
            </div>
            <div className={classes.accountItems}>
              <Button
                component={RouterLink}
                to="/account"
                color="inherit"
                style={isActive(history, '/account')}
              >
                <Typography>Account</Typography>
              </Button>
              <Button
                component={RouterLink}
                to="/login"
                color="inherit"
                style={isActive(history, '/login')}
              >
                <Typography>Login</Typography>
              </Button>
            </div>
          </div>
        </Toolbar>
      </AppBar>
      <div className={classes.offset} />
    </div>
  );
});

export default NavBar;
